  font-weight: normal;
  color: #fff;
}

.logo {
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  font-size: 1.5625rem;
  color: #fff;
}

.header__phone-icon {
  background-color: #618967;
  border-radius: 50%;
}
.header__number p {
  font-weight: 300;
  font-size: 0.9rem;
}
.header__number strong {
  font-weight: bold;
  font-size: 1rem;
}
.header__basket {
  background-color: #618967;
  border-radius: 10px;
}

.form-control-dark {
  background-color: #403C3B;
  border-color: #504B4A;
}

.navigation {
  position: sticky;
  top: 0;
  z-index: 2;
  background: rgb(64, 60, 59);
}
.navigation__menu {
  width: fit-content;
  margin: 0 auto;
}
.navigation__menu a {
  color: #fff;
  font-weight: 300;
}
.navigation__menu a:hover {
  color: #72A479;
}
.navigation__menu .active {
  color: #fff;
  border-bottom: #618967 solid 2px;
  font-weight: bold;
  pointer-events: none;
  cursor: default;
}

.nav-item {
  list-style-type: none;
}

small, .small {
  font-weight: lighter;
}

.banner {
  background-color: #211F20;
}
.banner__title {
  position: relative;
}
.banner__title .title {
  font-family: "Zing Rust Demo Base", sans-serif;
  width: fit-content;
  transform: rotate(-8deg);
  position: absolute;
  top: 15%;
  left: 5%;
}
.banner__title .title-stroke {
  font-family: "Zing Rust Demo Base", sans-serif;
  width: fit-content;
  position: absolute;
  color: rgba(255, 255, 255, 0);
  -webkit-text-stroke: rgba(255, 255, 255, 0.8) 1px;
  transform: translate(-5px, 5px);
  transform: rotate(-8deg);
  top: calc(15% + 5px);
  left: calc(5% - 5px);
}
.banner__subtitle {
  font-family: "Zing Rust Demo Base", sans-serif;
  width: fit-content;
  position: absolute;
  bottom: 20%;
  left: 20%;
  transform: rotate(-8deg);
}
.banner__subtitle::after {
  content: "";
  background-color: #72A479;
  width: 105%;
  height: 80%;
  display: inline-block;
  position: absolute;
  z-index: -1;
  bottom: 0.4em;
  left: -0.25em;
  transform: skewX(-20deg);
}
.banner__subtitle:before {
  content: "";
  border-style: solid;
  border-width: 1px;
  border-image: linear-gradient(45deg, rgba(33, 31, 32, 0), rgb(255, 255, 255)) 1;
  width: 110%;
  height: 80%;
  display: inline-block;
  position: absolute;
  z-index: -1;
  bottom: -0.5em;
  left: -1em;
  transform: skewX(-20deg);
}
.banner__background {
  min-height: 250px;
  height: 100%;
  max-height: 1000px;
  overflow: hidden;
}
.banner__background img {
  width: 100%;
  min-height: 250px;
  max-height: 480px;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.menu-slider {
  border-bottom: solid 1px;
  border-image: linear-gradient(90deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0)) 1;
}
.menu-slider__title {
  width: fit-content;
  position: relative;
}
.menu-slider__title:before {
  content: "";
  display: inline-block;
  position: absolute;
  z-index: -1;
  background-color: #618967;
  width: 4px;
  height: 33px;
  left: -0.8em;
  bottom: 50%;
  transform: translateY(50%);
}

